@import "./reset.scss";

@font-face {
  font-family: "Google Sans";
  src: url("../fonts/GoogleSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Google Sans";
  src: url("../fonts/GoogleSans-Medium.ttf") format("truetype");
  font-weight: 500;
